.card{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    width: 260px;
    height: 370px;
    box-sizing: border-box;
    background-color: #17141d;
    position: relative;
    box-shadow: -1rem 0 3rem #000;
    border-radius: 10px;
    opacity: 0.95;
    overflow: hidden;
}

.card:hover{
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    transition: 0.4s ease-out;
    opacity: 1;
}

.contentCard{
    align-items: left;
    width: 100%;
    height: 35%;
    overflow: hidden;
}
.cardImg{
    width: 100%;
    height: 45%;
    border-color: gray;
    box-sizing: border-box;
}
